<template>
  <div class="show">
  	<div class="bg">
  		<header>
  			<div class="hz">
  				<b class="title">{{item && item.title}}</b>
  				<div class="time">
  					<span class="">|发布于{{item && item.create_at.substring(0,10)}}</span>
 	  				<span class="author">&nbsp|作者：{{item && item.author.loginname}}</span>
  				</div>
  			</div>
	  	</header>
	  	
	  	<div class="content">
	  		<div class="cz">
	  			<div class="content_f" v-html="item && item.content"></div>
	  		</div>
	  	</div>
	  	
	  	<div class="count">
	  		<div class="coz">
				<div class="count_f">{{item && item.reply_count}}  回复</div>
	  		</div>
	  	</div>
	  	<div class="reply">
	  		<ul>
	  			<li v-for="item in (item && item.replies)">
	  				<div class="top">
	  					<img class="top_f" v-lazy="item.author.avatar_url" src="" alt="" />
	  					<p class="top_s">{{item && item.author.loginname}}</p>
	  					<p class="top_t" v-html="item && item.content"></p>
	  				</div>
	  			</li>
	  		</ul>
	  	</div>
  	</div>
  	
  </div>
</template>

<script>
// 引入 取数据的模块
import {getTopicData} from '@/getdata/getTopic.js';	

export default {
  name: 'Show',
  data () {
    return {
      item:[]
    }
  },
  mounted:function(){
  	var id=this.$route.params.id;
  	getTopicData(id).then((response)=>{
  		console.log(response);
  		   this.item = response.data.data;

  	})
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
	/*引入 公共样式*/
  	@import '../../assets/sass/base.scss';
  	.show{
  		padding-bottom:rem(160px);
  	}
  	.bg{
  		overflow:hidden;
  	}
  	.show .markdown-text img{
  		width:100%;
  	}
  	header{
  		width:97%;
  		background-color:#eeefff;
  		margin:0 auto;
  		margin-top:rem(120px);
  		margin-bottom:rem(9px);
  		
  		.hz{
  			margin-left:rem(15px);
  			margin-right:rem(15px);
  			padding-top:rem(23px);
  			.title{
	  			font-size:rem(44px);
	  		}
	  		.time{
	  			margin-top:rem(15px);
	  			padding-bottom:rem(15px);
	  			font-size:rem(12px);
	  			color:gray;
	  		}
  		}
  	}
  	.content{
  		width:97%;
  		background-color:#eeefff;
  		margin:0 auto;
  		overflow:hidden;
  		font-size:rem(20px);
  		.cz{
  			margin-left:rem(30px);
  			margin-right:rem(20px);
  			.contentf{
  				font-size:rem(25px);
  				padding-top:rem(21px);
  				padding-bottom:rem(48px);
  			}
  		}
  	}
  	.count{
  		width:97%;
  		padding-top:rem(30px);
  		padding-bottom:rem(30px);
  		background-color:#efefef;
  		margin:0 auto;
  		.coz{
  			margin-left:rem(15px);
  			font-size:rem(18px);
  		}
  	}
  	.reply ul{
  		width:97%;
  		background-color:#eeefff;
  		margin:0 auto;
  		overflow:hidden;
  		.top{
  			margin-top:rem(23px);
  			margin-left:rem(23px);
  			padding-bottom:rem(70px);
	  		border-bottom:rem(1px) solid lightgray;
	  		img{
	  			width:rem(90px);
	  		}
	  		.top_s{
	  			margin-top:rem(-80px);
	  			margin-left:rem(100px);
	  			font-size:rem(10px);
	  		}
	  		.top_t{
	  			margin-top:rem(55px);
	  			font-size:rem(16px);
	  		}
	  	}
  	}
  	
</style>



