<template>
  <div class="ask">
  	<!--主题列表-->
  	<topic-list :items="items"></topic-list>
  	<infinite-loading v-if="isShow" @infinite="infiniteHandler"></infinite-loading>
  </div>
</template>

<script>
// 引入 取数据的模块
import {getTopics} from '@/getdata/getTopic.js';	
// 引入 主题列表组件
import topicList from '@/components/topics/List';
//引入分页滑动
import InfiniteLoading from 'vue-infinite-loading';

export default {
  name: 'Ask',
  data () {
    return {
    // 主题列表数据
      items:[],
      page:1,
      isShow:true
    }
  },
	// 钩子函数
	mounted: function () { 
    getTopics({tab:'ask',page:this.page}).then((response)=>{
//    console.log(response);
      this.items = response.data.data;
    })
	},
	methods: {
    infiniteHandler($state) {
//  	var p = this.page;
      setTimeout(() => {
        // 查询 全部主题数据
        getTopics({tab:'all',limit:15,page:++this.page}).then((response)=>{
//        console.log(response.data.data);
          // 把查询到的数据放到 items里
          this.items = this.items.concat(response.data.data);
					// 判断下一页，还有没有数据
          if(response.data.data.length ==0){
            this.isShow = false;
            // 结束了
            return;
          } 
					$state.loaded();
        });
      }, 500);
    },
  },
	// 加载组件
  components:{
    // 主题列表组件
    topicList,InfiniteLoading
  }
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	/*引入 公共样式*/
  @import '../../assets/sass/base.scss';
	.ask{
		margin-top:rem(90px);
		margin-bottom:rem(120px);
	}
</style>
 


